  display: block;
}

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

/* Misc
   ========================================================================== */
/**
 * Add the correct display in IE 10+.
 */
template {
  display: none;
}

/**
 * Add the correct display in IE 10.
 */
[hidden] {
  display: none;
}

/* ================================
   VARIABLES GLOBALES (SCSS)
   ================================ */
/* Fuente principal del proyecto
   - Se usará en la mayoría del sitio (body, títulos, etc.) */
/* Breakpoint para teléfonos (pantallas pequeñas) */
/* Breakpoint para tablets (pantallas medianas) */
/* Breakpoint para escritorio (pantallas grandes) */
/* Colores base del proyecto */
/* Separación general del diseño (espaciados y márgenes) */
/* Pesos de tipografía disponibles según la fuente importada */
/* ================================
   MIXINS (REUTILIZABLES)
   ================================ */
/* Mixin para estilos desde tamaño "teléfono" en adelante
   - Usa la variable $telefono como breakpoint mínimo
   - @content permite insertar estilos dentro del mixin */
/* Mixin para estilos desde tamaño "tablet" en adelante
   - Usa la variable $tablet como breakpoint mínimo */
/* Mixin para estilos desde tamaño "desktop" en adelante
   - Usa la variable $desktop como breakpoint mínimo */
/* Mixin para crear un grid de forma rápida y parametrizable
   - $columnas: número de columnas del grid
   - $espaciado: separación (gap) entre filas y columnas */
/* Mixin para botones (usado en anuncios, etc.) */
/* ================================ 
   ESTILOS GLOBALES (BASE) 
   ================================ */
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

/* ================================
   UTILIDADES (CLASES REUTILIZABLES)
   ================================ */
/* .seccion:
   Se usa para dar separación vertical estándar entre secciones del sitio
   - Aplica margen superior e inferior usando la variable $separacion */
.seccion {
  /* Margen superior con la mitad del valor definido en $separacion */
  margin-top: 2.5rem;
  /* Margen inferior con la mitad del valor definido en $separacion */
  margin-bottom: 2.5rem;
}

/* .contenido-centrado:
   Sirve para limitar el ancho del contenido y hacerlo más legible
   (útil para textos largos, artículos, secciones de lectura) */
.contenido-centrado {
  /* Define un ancho máximo fijo para que no se estire demasiado en pantallas grandes */
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.dark-mode {
  background-color: rgb(63.75, 63.75, 63.75);
}
.dark-mode p, .dark-mode td {
  color: #ffffff;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode blockquote {
  color: #e1e1e1;
}
.dark-mode .anuncio {
  background-color: rgb(58.65, 58.65, 58.65);
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio {
  color: #71b100;
}
.dark-mode .anuncio .icono {
  filter: invert(70%);
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: rgb(38.25, 38.25, 38.25);
  border: 1px solid #333333;
  color: #ffffff;
}

