/* ================================
   app.scss (ARCHIVO PRINCIPAL)
   ================================ */
/* IMPORTANTE:
   - TODOS los @use deben ir al inicio del archivo.
   - NO mezclar @import con @use.
*/
/* Base */
/* Layout */
/* ================================
   HEADER (ENCABEZADO)
   ================================ */
/* Cuando el navegador NO soporta WebP:
   se aplica la imagen JPG como fondo en la clase .inicio */
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

/* Cuando el navegador SÍ soporta WebP:
   se aplica la imagen WebP como fondo en la clase .inicio */
.webp .inicio {
  background-image: url(../img/header.webp);
}

/* Estilos generales del header */
.header {
  /* Color de fondo por defecto (útil mientras carga la imagen o en otras páginas) */
  background-color: #333333;
  /* Padding: 1rem arriba, 0 laterales, 3rem abajo */
  padding: 1rem 0 3rem 0;
  /* &.inicio significa: .header.inicio
     Se aplica solo cuando el header tenga ambas clases */
}
.header.inicio {
  /* Centra la imagen de fondo */
  background-position: center center;
  /* Hace que la imagen cubra todo el espacio disponible */
  background-size: cover;
  /* Desde tablet en adelante se define una altura mínima */
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
.header.inicio {
  /* Estilos específicos para el contenedor interno del header */
}
.header.inicio .contenido-header {
  /* En tablet también se fuerza el alto mínimo
     (sirve para alinear bien el contenido con flex) */
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

/* Contenido interno del header (texto, navegación, etc.) */
.contenido-header {
  /* Flexbox para organizar el contenido de forma vertical */
  display: flex;
  /* Elementos en columna (uno debajo del otro) */
  flex-direction: column;
  /* Distribuye el contenido dejando espacio entre el top (barra) y el h1 */
  justify-content: space-between;
  /* Estilos del título principal dentro del header */
}
.contenido-header h1 {
  /* Alinea el texto a la izquierda */
  text-align: left;
  /* Color blanco para contraste con el fondo */
  color: #ffffff;
  /* Peso del texto usando variable SCSS */
  font-weight: 700;
  /* Espaciado vertical entre líneas */
  line-height: 1.5;
  /* Limita el ancho para mejor lectura y estética */
  max-width: 60rem;
}

body {
  font-family: "Lato", sans-serif;
}

/* ================================
   app.scss (ARCHIVO PRINCIPAL)
   ================================ */
/* IMPORTANTE:
   - TODOS los @use deben ir al inicio del archivo.
   - NO mezclar @import con @use.
*/
/* Base */
/* Layout */
/* ================================
   FOOTER (PIE DE PÁGINA)
   ================================ */
/* Contenedor principal del footer */
.footer {
  /* Espaciado interno (arriba y abajo):
     - $separacion / 2 define un padding proporcional al sistema de espaciado del proyecto
     - 0 indica que no hay padding lateral */
  padding: 2.5rem;
  /* Color de fondo del footer usando variable SCSS */
  background-color: #333333;
  margin-bottom: 0;
  /* Contenedor interno del footer (generalmente para alinear contenido y navegación) */
}
.footer .contenedor-footer {
  /* Espaciado superior adicional para separar el contenido dentro del footer */
  margin-top: 2.5rem 0;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer {
  /* Texto de derechos reservados */
}
.footer .copyright, .footer p {
  /* Color blanco para contrastar con el fondo oscuro */
  color: #ffffff;
  text-align: center;
  font-size: 1.5rem;
}

/* ================================
   app.scss (ARCHIVO PRINCIPAL)
   ================================ */
/* IMPORTANTE:
   - TODOS los @use deben ir al inicio del archivo.
   - NO mezclar @import con @use.
*/
/* Base */
/* Layout */
/* ================================
   BARRA (LOGO + NAVEGACIÓN)
   ================================ */
/*dark mode:
 habilita o inhabilita el modo oscuro en la web
*/
.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 2rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(99%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

/* .barra:
Contenedor que normalmente incluye el logo y el menú de navegación */
.barra {
  /* Activa Flexbox para alinear elementos */
  display: flex;
  /* En móvil: se organizan en columna (uno debajo del otro) */
  flex-direction: column;
  align-items: center;
  /* Separación superior para dar aire al header */
  padding-top: 3rem;
  /* Desde tablet en adelante cambia la distribución */
}
@media (min-width: 768px) {
  .barra {
    /* En pantallas medianas/grandes: elementos en fila (lado a lado) */
    flex-direction: row;
    /* Se separan al máximo:
       logo a la izquierda y menú a la derecha */
    justify-content: space-between;
    align-items: flex-end;
  }
}

/* ================================
   NAVEGACIÓN (MENÚ)
   ================================ */
/* .navegacion:
   Contenedor de los links del menú */
.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
  /* Desde tablet en adelante ajusta la disposición del menú */
}
@media (min-width: 768px) {
  .navegacion {
    /* Los enlaces se alinean horizontalmente */
    display: flex;
    /* Alinea verticalmente los links al centro */
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion {
  /* Estilos para cada link dentro de .navegacion */
}
.navegacion a {
  /* Color blanco para buen contraste sobre el header */
  color: #ffffff;
  /* Quita el subrayado por defecto */
  text-decoration: none;
  /* Tamaño de letra más grande en móvil */
  font-size: 2rem;
  /* Hace que el link sea un bloque (más fácil de tocar en móvil) */
  display: block;
  /* Centra el texto en móvil */
  text-align: center;
  /* Ajustes específicos desde tablet */
}
@media (min-width: 768px) {
  .navegacion a {
    /* Espaciado entre links */
    margin-right: 2rem;
    /* Reduce un poco el tamaño para pantallas grandes */
    font-size: 1.8rem;
    /* Al último enlace le quita el margen derecho
       para evitar espacio extra al final */
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: rgb(145.5593220339, 228, 0);
}

/** Menu de hamburguesa */
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  padding-top: 2rem;
  width: 3rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

