@use 'base/normalize'; // Importa normalize.css para estandarizar estilos entre navegadores
@use 'base/variables' as *; // Importa variables globales sin prefijo (ej: $grisOscuro, $blanco, $bold)
@use 'base/mixins' as *; // Importa mixins del proyecto (no se usan aquí, pero quedan disponibles)
@use 'base/globales'; // Importa estilos globales del proyecto
@use 'base/utilidades'; // Importa utilidades/clases helper
@use 'base/darkMode'; // Importa estilos del modo oscuro (si aplica a formularios)
@use 'sass:math'; // Importa módulo math (no se usa en este bloque)
@use 'sass:color'; // Importa módulo color (no se usa en este bloque)


/* Layout */


@include tablet { // Aplica los estilos solo a partir del breakpoint "tablet" definido en el mixin
    .iconos-nosotros { // Contenedor de los íconos de la sección "Nosotros"
        @include grid(3, 2rem); // Crea un grid de 3 columnas con separación (gap) de 2rem
    } // Cierra .iconos-nosotros
} // Cierra el bloque del mixin tablet




.icono { /*llamamos la clase icono del index.php*/
    text-align: center; /* Centra el texto */

    img { /*etiqueta html img*/
        height: 10rem; /*tamaño de la imagen*/
    }

    h3 { /*etiqueta html h3*/
        text-transform: uppercase; /*Transforma el texto del h3 en mayusculas*/
    }
}

.acciones-admin{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem; 
}

.btn-icon{
    display: flex;
    align-items: center;
    justify-content: center;
    align-items: center;
    justify-content: center;
    width: 4.8rem;                /* ancho del botón ícono */
    height: auto;                 /* se estira por align-items: stretch */
    align-self: stretch;          /* fuerza a estirarse */
    border-radius: .5rem;
    text-decoration: none;
    padding: 0;
}

/* el IMG/SVG se adapta a la altura del "botón" */
.icono-eliminar{
    width: 5rem;   /* ajusta aquí */
    height: 5rem;  /* ajusta aquí */
    padding-bottom: .5rem;      /* quita padding si está inflando el tamaño */
    max-height: none;
}